import React, {Component} from 'react';
import NavbarTwoPage from "./navbar-two-page";
import HeaderTwo from "./header-two";
import Section1Two from "./section1two";
import Section2Two from "./section2two";

class AllTwoPage extends Component {
    render() {
        return (
            <div>
                <NavbarTwoPage/>
                <HeaderTwo/>
                <Section1Two/>
                <Section2Two/>
            </div>
        );
    }
}

export default AllTwoPage;