import React, {Component} from 'react';

class LanguageCard extends Component {
    render() {
        const {header,content}=this.props;
        return (
            <div className="Java">
                <div className="java-hreff">
                    <h2>{header}</h2>
                    <div className="java-botom-href"><b>122 ta</b><p> topshiriq |</p> <b>13 ta</b>
                        <p>bo’lim</p></div>
                </div>
                <div className="line-section-2">

                </div>
                <div className="java-middle">
                    <div className="java-middle-href">
                        {content}
                    </div>
                    <button>
                        Kursni boshlash
                        <img src="/img-logo/arrow-right.svg" alt=""/>
                    </button>
                </div>

            </div>
        );
    }
}

export default LanguageCard;