import React, {Component} from 'react';
class DegreeCard extends Component {
    render() {
        const {degreeBig,degreeUzs,degreeMuonth,degreeMidlehref,degreeImg}=this.props
        return (
            <div className='degree-card'>
                <div className="degree-card-href-big">
                    {degreeBig}
                </div>
                <div className="uzs">
                    {degreeUzs}
                </div>
                <div className="mounth">
                    {degreeMuonth}

                </div>
                <div className="midle-href">
                    {degreeMidlehref}

                </div>
                <button>
                    Obuna bo’lish
                </button>
                <div className="degree-img">
                    {degreeImg}
                    <img src="/img-logo/degree-img.png" alt=""/>
                </div>
            </div>
        );
    }
}

export default DegreeCard;