import React, {Component} from 'react';

class MiddleSectionCard extends Component {
    render() {
        const {headerTitle,lineColor,content,lang,section,degree}=this.props;
        return (
            <div className="middle-sect">
                <div className="one">
                    <div className="one-href">
                        {headerTitle}
                    </div>
                    <div className=" one-line" style={{backgroundColor:lineColor}}>

                    </div>

                </div>
                <div className="big-line">
                </div>
                <div className="two">
                    <div className="two-href">
                        {content}
                    </div>
                </div>
                <div className="three">
                    <div className="href-left">
                        <div className="func">
                            Dasturlash tili
                        </div>
                        <div className="java">
                            {lang}
                        </div>
                    </div>
                    <div className="href-middle">
                        <div className="sec">
                            Bo’lim
                        </div>
                        <div className="string">
                            {section}
                        </div>
                    </div>
                    <div className="href-right">
                        <div className="deg">
                            Daraja
                        </div>
                        <div className="mid">
                            {degree}
                        </div>
                    </div>
                </div>
                <div className="bottom-button">
                    <button>
                        Bajarish
                        <img src="/img-logo/arrow-right.svg" alt=""/>
                    </button>
                </div>
            </div>
        );
    }
}

export default MiddleSectionCard;